body {
    font-family: 'Inter', sans-serif;
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
}

.app {
    text-align: center;
}

.app-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .app-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.app-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.neon-container {
    box-shadow: 0 0 .2rem #fff,
        0 0 .2rem #fff,
        0 0 2rem #e84118,
        0 0 0.8rem #e84118,
        0 0 2.8rem #e84118,
        inset 0 0 1.3rem #e84118;
    padding: 50px;
    border-radius: 12px;
}

.neon-text {
    font-family: "Sacramento", sans-serif;
    animation: flicker 1.5s infinite alternate;
    font-weight: 400;
    text-transform: lowercase;
    letter-spacing: 4px;
    margin-bottom: 40px;
    font-size: 52px;
}

@keyframes flicker {
    0%, 18%, 22%, 25%, 53%, 57%, 100% {
        text-shadow:
                0 0 4px #fff,
                0 0 11px #fff,
                0 0 19px #fff,
                0 0 40px #e84118,
                0 0 80px #e84118,
                0 0 90px #e84118,
                0 0 100px #e84118,
                0 0 150px #e84118;
    }

    20%, 24%, 55% {
        text-shadow: none;
    }
}

.name-grid {
}

.name-grid-row {
    display: flex;
    margin-bottom: 5px;
}

.name-grid-item {
    border-radius: 8px;
    margin-right: 5px;
    width: 30px;
    height: 30px;
}

.name-grid-item:last-child {
    margin-right: 0;
}

.name-grid-item-empty {
    background: #353b48;
}

.name-grid-item-full {
    animation: flicker-grid-item 1.5s infinite alternate;
}


@keyframes flicker-grid-item {
    0%, 18%, 22%, 25%, 53%, 57%, 100% {
        background:  rgba(232, 65, 24, 0.8);
        box-shadow: 0 0 50px rgba(232, 65, 24, 0.8);
    }

    20%, 24%, 55% {
        background:  rgba(232, 65, 24, 0.1);
        box-shadow: 0 0 50px rgba(232, 65, 24, 0.1);
    }
}